main {

	h1,
	h2,
	h3 {
		font-weight: 600;
	}
 
	h1,
	h2 {
		text-transform: uppercase;
	}
	h1 {
		font-size: 2.8rem;

		@include media-breakpoint('md') {
			font-size: 2rem;
		}
	}

	h2 {
		font-size: 1.85rem;
		line-height: 1.2;
		margin-bottom: 0.5em;

		@include media-breakpoint('md') {
			font-size: 1.65rem;
		}
	} s 

	h3,
	h4 {
		font-size: 1.25rem;
	}

	h4 {
		font-weight: 500;
	}
}

ul,
p,
body {
	line-height: 1.75;
	font-weight: 300;
	color: inherit;
}

.cta>a {
	font-size: 1.35em;
	letter-spacing: 0.05em;
	color: $primary;

	@include media-breakpoint('md') {
		font-size: 1.2em;
	}
}

/* links */

a {
	transition: color 0.5s;
	color: var(--primary);
}

main p>a,
main li>a {
	color: inherit;
	padding-bottom: 0.1em;
	border-bottom: 1px solid $info;
	box-sizing: border-box;
}

a:hover {
	text-decoration: none;
	color: var(--primary-dark);
}

b,
strong {
	font-weight: bold;
}