  font-size: 1.85rem;
  line-height: 1.2;
  margin-bottom: 0.5em;
}
@media (min-width: 991px) {
  main h2 {
    font-size: 1.65rem;
  }
}
main s h3,
main h4 {
  font-size: 1.25rem;
}
main h4 {
  font-weight: 500;
}

