/* links */
a {
  transition: color 0.5s;
  color: var(--primary);
}

main p > a,
main li > a {
  color: inherit;
  padding-bottom: 0.1em;
  border-bottom: 1px solid #0f81c7;
  box-sizing: border-box;
}

a:hover {
  text-decoration: none;
  color: var(--primary-dark);
}

b,
strong {
  font-weight: bold;
}