$font-family-sans-serif: 'Work Sans',
'Open Sans',
'Arial',
'Helvetica',
sans-serif !default;
$font-family-base: $font-family-sans-serif;
$headings-font-family: 'Montserrat',
'Arial',
'Helvetica',
sans-serif !default;
$headings-font-weight: bold;
$enable-responsive-font-sizes: true;
$enable-grid-classes: true;
$font-size-base: 1rem;

// Cambiamos algunos colores para poder utilizar las clases tipo btn-primary o bg-info
// ver https://getbootstrap.com/docs/4.4/utilities/colors/
$primary: #00abd3;
$secondary: #205155;
$success: #015668;
$danger: #ac2539;
$info: #0f81c7;
$warning: #d1cf52;
$light: #eeeeee;
$dark: #111;
$white: #fff;
$lightgrey: #cccccc;
$grey: #666;
$orange: #fbad2d;
$darkgrey: #333;
$black: #000;
$footer-text: rgba(255, 255, 255, 0.7);
$primary-btn-icons: #b98c03;

//header
$header-icons: $primary;

//footer
$footer-icons: var(--footer-bg);
$footer-icons-background: var(--extra1);
$text-footer-primary: var(--footer-text-color);
$text-footer-secondary: var(--extra3);
$border-footer-color: var(--extra3);
$background-footer: var(--footer-bg);

// Cambiamos la variable que controla todos los márgenes y paddings
$spacer: 1.2rem;

// Agregamos un par (6 y 7) de variantes a los espaciados para que al compilar se generen clases como mb-7 o pr-6. Ver https://getbootstrap.com/docs/4.4/utilities/spacing/

$spacers: (0: 0,
	1: ($spacer * 0.25,
	),
	2: ($spacer * 0.5,
	),
	3: $spacer,
	4: ($spacer * 1.5,
	),
	5: ($spacer * 3,
	),
	6: ($spacer * 5,
	),
	7: ($spacer * 10,
	),
);

// Cambiamos border-radious de los elementos
$border-radius-base: 0;